import React, { useContext, useEffect, useRef, useState } from 'react';
import {
  Button,
  OverLay,
  Response,
  CloseBtn,
  OverLayContainer,
  ClearBtn,
} from './styles';
import closeSlider from '../../Assets/expand-contract button-2.png';
import openSlider from '../../Assets/expand-contract button-3.png';
import error from '../../Assets/error.png';
import ShareData from '../../ShareData';
import ConsoleText from './ConsoleText';

const Console = () => {
  const { isOverlayVisible, handleOverLay, messages, clearConsole } =
    useContext(ShareData);

  return (
    <>
      {!isOverlayVisible && (
        <Button onClick={handleOverLay}>
          <img
            style={{ width: '100%', transform: 'rotate(180deg)' }}
            src={openSlider}
          />
        </Button>
      )}

      {isOverlayVisible && (
        <OverLay style={{ opacity: `${isOverlayVisible ? 1 : 0}` }}>
          <CloseBtn onClick={handleOverLay}>
            <img
              style={{
                width: '100%',
                transform: 'rotate(180deg)',
              }}
              src={closeSlider}
            />
          </CloseBtn>
          <ConsoleText />
        </OverLay>
      )}

      {isOverlayVisible && <OverLayContainer></OverLayContainer>}
    </>
  );
};

export default Console;
