import { Fragment, useContext, useEffect, useRef, useState } from "react";
import renderImage from "../RenderImage";
import "./SideBar.css";
import ShareData from "../../ShareData";
import { saveAs } from "file-saver";

function SideBar() {
  const {
    setCode,
    worker,
    sidebar_filesName,
    setSidebar_FilesName,
    setSubHeader_filesName,
    subHeader_filesName,
    workSpaceFile,
    setWorkSpaceFile,
    setIsChange,
  } = useContext(ShareData);
  const [showInputFileName, setShowInputFileName] = useState(false);
  const [newFileName, setNewFileName] = useState("");
  const [isShowError, setIsShowError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showInputError, setShowInputError] = useState(false);
  const [isFileMenuShow, setIsFileMenuShow] = useState("");
  const [isFileFileRename, setIsFileFileRename] = useState("");
  const [name_fileRename, setName_fileRename] = useState("");
  const zingEditar = JSON.parse(sessionStorage.getItem("zingEditar")) || {};
  const sideBarRef = useRef();
  const input33Ref = useRef();
  const menuRef = useRef(null);
  const menuBtnRef = useRef(null);

  const addNewFile = () => {
    // if (Object.keys(sidebar_filesName).length >= 5) {
    //     setErrorMessage("You are create only 5 files!")
    //     setIsShowError(true)
    //     setTimeout(() => setIsShowError(false), 600)
    //     return
    // }
    setShowInputFileName(!showInputFileName);

    if (sideBarRef.current) {
      sideBarRef.current.scrollTop = 0;
    }
  };

  //handel the file name input changes
  const handelChange = (e) => {
    // let inputText = e.target.value
    // if()
    let key = e.target.value;
    const stringRegex = /^[a-zA-Z_][a-zA-Z0-9_]/;
    const pythonFileNameRegex = /^[a-zA-Z_][a-zA-Z0-9_]*(\.(p|py)(,py)?)?$/;
    if (!pythonFileNameRegex.test(key)) {
      setShowInputError(true);
    } else {
      setShowInputError(false);
    }

    setNewFileName(e.target.value);
  };

  //create a new file
  const handelFileNameSubmit = (e) => {
    e.preventDefault();
    if (!newFileName.trim()) {
      setErrorMessage("A file name must be provided!");
      setIsShowError(true);
      setTimeout(() => setIsShowError(false), 600);
      return;
    }
    if (newFileName.trim().length > 20) {
      setErrorMessage("File name can contain only 20 characters!");
      setIsShowError(true);
      setTimeout(() => setIsShowError(false), 600);
      return;
    }

    let key = newFileName.trim();
    const stringRegex = /^[a-zA-Z_][a-zA-Z0-9_]*$/;
    const pythonFileNameRegex = /^[a-zA-Z_][a-zA-Z0-9_]*(\.py)?$/;
    if (stringRegex.test(key)) {
      key = key + ".py";
    } else if (pythonFileNameRegex.test(key)) {
      key = key;
    } else {
      setErrorMessage("This is not a valid python file name!");
      setIsShowError(true);
      setTimeout(() => setIsShowError(false), 600);
      return;
    }

    //if file name present in the sidebar than not create the file
    let isFileNamePresent = false;
    Object.keys(sidebar_filesName).map((eachFileName) => {
      if (eachFileName === key) {
        isFileNamePresent = true;
      }
    });
    if (isFileNamePresent) {
      setErrorMessage("This file name already exists!");
      setIsShowError(true);
      setTimeout(() => setIsShowError(false), 600);
      return;
    }

    setIsChange(true);
    let new_Sidebar_FilesName = { ...sidebar_filesName };
    new_Sidebar_FilesName[key] = { filename: key, code: "" };
    setSidebar_FilesName(new_Sidebar_FilesName);
    setWorkSpaceFile({ filename: key, code: "" });
    setSubHeader_filesName([...subHeader_filesName, key]);
    // setSidebar_FilesName({ ...sidebar_filesName, key: { filename: newFileName.trim(), code: '' } })
    // zingEditar = { ...zingEditar, sidebar_filesName: [...sidebar_filesName, { filename: newFileName.trim(), code: '' }] }
    // sessionStorage.setItem('zingEditar', JSON.stringify(zingEditar));
    setNewFileName("");
    setShowInputFileName(false);
  };

  //open the file in ide workspace
  const handelSubHeader_filesName = (eachFileObject) => {
    let isFilePresent_subheder = false;
    subHeader_filesName.map((subHeader_eachFileName) => {
      if (subHeader_eachFileName === eachFileObject.filename) {
        isFilePresent_subheder = true;
      }
    });

    setWorkSpaceFile(eachFileObject);
    if (isFilePresent_subheder) {
      return;
    }
    setSubHeader_filesName([...subHeader_filesName, eachFileObject.filename]);
    // setCode(eachFileObject.code)
  };

  //open the sidebar file menu
  const handel_3_dotMenuClick = (e, eachFileObjectKey) => {
    if (eachFileObjectKey === isFileMenuShow) {
      setIsFileMenuShow("");
    } else {
      setIsFileMenuShow(eachFileObjectKey);
    }
  };

  //delete file from fremweir and ide
  const delete_file = (e, eachFileObjectKey) => {
    let newSidebar_filesName = { ...sidebar_filesName };
    let newSubHeader_filesName = [...subHeader_filesName];
    let length = newSubHeader_filesName.length;

    //remove from sidebar
    setIsChange(true);
    delete newSidebar_filesName[eachFileObjectKey];
    setSidebar_FilesName(newSidebar_filesName);

    //remove from subheder
    newSubHeader_filesName.map((newSubHeader_eahFileName, ind) => {
      if (newSubHeader_eahFileName === eachFileObjectKey) {
        newSubHeader_filesName.splice(ind, 1);
        if (length === 1) {
          setWorkSpaceFile({});
        } else if (ind + 1 < length) {
          setWorkSpaceFile(sidebar_filesName[newSubHeader_filesName[ind]]);
        } else {
          setWorkSpaceFile(sidebar_filesName[newSubHeader_filesName[ind - 1]]);
        }
        setSubHeader_filesName(newSubHeader_filesName);
      }
    });

    //remove from zing microPython board
    let removeFileCommend = `try:\n    import os\nexcept ImportError:\n    import uos as os\nos.remove("${eachFileObjectKey}")`;
    let asciRemoveFileCommend = [...removeFileCommend].map((char) =>
      char.charCodeAt(0)
    );
    worker.postMessage({
      type: "ignoreConsolePrint",
      value: [3, 1, ...asciRemoveFileCommend, 4, 2],
    });
  };

  //rename the sidebar file
  const renameFileClick = (e, eachFileObjectKey) => {
    if (eachFileObjectKey === isFileFileRename) {
      setIsFileFileRename("");
    } else {
      setIsFileFileRename(eachFileObjectKey);
      setName_fileRename(eachFileObjectKey);
    }
  };

  const handelRenameFileSubmit = (e, eachFileObjectKey) => {
    e.preventDefault();

    if (name_fileRename.trim() === eachFileObjectKey) {
      setName_fileRename("");
      setIsFileFileRename("");
      setIsFileMenuShow("");
      return;
    }

    if (!name_fileRename.trim()) {
      setErrorMessage("A file name must be provided!");
      setIsShowError(true);
      setTimeout(() => setIsShowError(false), 600);
      return;
    }
    if (name_fileRename.trim().length > 20) {
      setErrorMessage("File name can contain only 20 characters!");
      setIsShowError(true);
      setTimeout(() => setIsShowError(false), 600);
      return;
    }

    //if file name present in the sidebar than not create the file
    let isFileNamePresent = false;
    Object.keys(sidebar_filesName).map((eachFileName) => {
      if (eachFileName === name_fileRename.trim()) {
        isFileNamePresent = true;
      }
    });
    if (isFileNamePresent) {
      setErrorMessage("This file name already exists!");
      setIsShowError(true);
      setTimeout(() => setIsShowError(false), 600);
      return;
    }

    let key = name_fileRename.trim();
    const stringRegex = /^[a-zA-Z_][a-zA-Z0-9_]*$/;
    const pythonFileNameRegex = /^[a-zA-Z_][a-zA-Z0-9_]*(\.py)?$/;
    if (stringRegex.test(key)) {
      key = key + ".py";
    } else if (pythonFileNameRegex.test(key)) {
      key = key;
    } else {
      setErrorMessage("This is not a valid python file name!");
      setIsShowError(true);
      setTimeout(() => setIsShowError(false), 600);
      return;
    }

    let change_Sidebar_FilesName = Object.entries(sidebar_filesName).reduce(
      (acc, [presentKey, value]) => {
        if (presentKey === eachFileObjectKey) {
          acc[key] = value; // Assign to new key
        } else {
          acc[presentKey] = value; // Keep existing key-value pairs
        }
        return acc;
      },
      {}
    );

    setIsChange(true);
    change_Sidebar_FilesName[key].filename = key;
    setSidebar_FilesName(change_Sidebar_FilesName);

    if (subHeader_filesName.includes(eachFileObjectKey)) {
      let newSubHeader_filesName = subHeader_filesName.map((ele) => {
        if (ele === eachFileObjectKey) return key;
        else return ele;
      });

      if (workSpaceFile.filename == eachFileObjectKey) {
        setWorkSpaceFile({ ...workSpaceFile, filename: key });
      }

      setSubHeader_filesName(newSubHeader_filesName);
    }

    setName_fileRename("");
    setIsFileFileRename("");
    setIsFileMenuShow("");
  };
  //save the sidebar file
  const saveFileClick = (e, eachFileObjectKey) => {
    // const currentOpenFileName = props.codeEditor.currentOpenFileName;
    const fileData = sidebar_filesName[eachFileObjectKey].code;
    const blob = new Blob([fileData], { type: "text/plain" });
    saveAs(blob, eachFileObjectKey);
    setIsFileMenuShow("");
  };

  // Hide div when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        menuRef.current &&
        !menuRef.current.contains(event.target) &&
        menuBtnRef.current &&
        !menuBtnRef.current.contains(event.target)
      ) {
        setIsFileMenuShow("");
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  return (
    <div className="create-file-container">
      <div className="program-name" onClick={(e) => addNewFile()}>
        <p>Create File</p>
        <img src={renderImage("addButton")} />
      </div>

      <div className="files-container" ref={sideBarRef}>
        {/* ---------------error message  show  ------------------- */}
        {isShowError && <div className="newFilenameError">{errorMessage}</div>}

        <div
          className="input_fileName"
          style={showInputFileName ? { display: "flex" } : { display: "none" }}
        >
          <form
            onSubmit={(e) => handelFileNameSubmit(e)}
            style={{
              width: "99.8%",
            }}
            // border-bottom: 1px solid rgba(59, 59, 59, 0.5);
          >
            <input
              className={
                showInputError ? "input_error_class" : "input_text_class"
              }
              spellCheck="false"
              ref={input33Ref}
              value={newFileName}
              onChange={(e) => handelChange(e)}
            ></input>
          </form>
        </div>
        {Object.keys(sidebar_filesName).map((eachFileObjectKey, ind) => (
          // <Fragment key={ind}>
          //   <FileContainer eachFileObjectKey={eachFileObjectKey} />
          // </Fragment>
          <Fragment key={ind}>
            {isFileFileRename === eachFileObjectKey ? (
              <form
                onSubmit={(e) => handelRenameFileSubmit(e, eachFileObjectKey)}
                style={{
                  width: "99.8%",
                  borderBottom: "1px solid rgba(59, 59, 59, 0.5)",
                }}
              >
                <input
                  className={
                    showInputError ? "input_error_class" : "input_text_class"
                  }
                  spellCheck="false"
                  // ref={inputRenameRef}
                  value={name_fileRename}
                  onChange={(e) => setName_fileRename(e.target.value)}
                ></input>
              </form>
            ) : (
              <div
                // key={ind}
                onClick={(e) =>
                  handelSubHeader_filesName(
                    sidebar_filesName[eachFileObjectKey]
                  )
                }
                className="each-file"
              >
                <p>{eachFileObjectKey}</p>
                {/* eachFileObjectKey !== "main.py" && */}

                <div className="fileMenuDiv">
                  <img
                    className="cross_button"
                    onClick={(e) => {
                      e.stopPropagation();
                      // delete_file(e, eachFileObjectKey);
                      handel_3_dotMenuClick(e, eachFileObjectKey);
                    }}
                    style={{}}
                    src={renderImage("threeDonMenu")}
                    ref={menuBtnRef}
                  />
                  <div className="fileMenu_toolTip">Menu</div>

                  {/* -------------------file  Menu div ------------------- */}
                  <div
                    ref={menuRef}
                    className={`fileMenu_icons ${
                      isFileMenuShow == eachFileObjectKey
                        ? "fileMenu_icons_show"
                        : ""
                    } ${
                      eachFileObjectKey === "main.py" ? "mainFile_meuDiv" : ""
                    }`}
                  >
                    <div className={`fileMenu_icons_inner `}>
                      <p
                        className="each_file_menuP"
                        onClick={(e) => {
                          e.stopPropagation();
                          saveFileClick(e, eachFileObjectKey);
                        }}
                      >
                        Save
                      </p>
                      {eachFileObjectKey !== "main.py" && (
                        <>
                          <p
                            className="each_file_menuP"
                            onClick={(e) => {
                              e.stopPropagation();
                              renameFileClick(e, eachFileObjectKey);
                            }}
                          >
                            Rename
                          </p>
                          <p
                            className="each_file_menuP"
                            onClick={(e) => {
                              e.stopPropagation();
                              delete_file(e, eachFileObjectKey);
                            }}
                          >
                            Delete{" "}
                          </p>
                        </>
                      )}
                      <div
                        className={`menu_arrow ${
                          eachFileObjectKey === "main.py"
                            ? "mainFile_menu_arrow"
                            : ""
                        }`}
                      ></div>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </Fragment>
        ))}
      </div>
    </div>
  );
}

export default SideBar;
