import React, { useRef } from "react";
import "./Dropdown.css";
import { dropdown_menu } from "../Utils/data";
import renderImage from "../RenderImage";
const Dropdown = ({
  toggleDropDown,
  registerRef,
  getSampleCode,
  scrollIndicator,
}) => {
  // const scrollIndicator = useRef()

  const sampleRef = registerRef("sampleRef");
  const droDownInner = registerRef("droDownInner");
  const inputRef1 = registerRef("inputRef1");
  const outputRef1 = registerRef("outputRef1");
  const inbuiltRef1 = registerRef("inbuiltRef1");
  const externalRef1 = registerRef("externalRef1");
  const outputExternalRef1 = registerRef("outputExternalRef1");
  const mainRef1 = registerRef("mainRef1");
  const inputRef2 = registerRef("inputRef2");
  const outputRef2 = registerRef("outputRef2");
  const outputInbuiltRef1 = registerRef("outputInbuiltRef1");
  const inbuiltRef2 = registerRef("inbuiltRef2");
  const outputInbuiltRef2 = registerRef("outputInbuiltRef2");
  const externalRef2 = registerRef("externalRef2");
  const outputExternalRef2 = registerRef("outputExternalRef2");
  const mainRef2 = registerRef("mainRef2");
  // const scrollIndicator = registerRef("scrollIndicator");

  const peecee_input_inbuild =
    dropdown_menu[`PEECEE_dropdown_menu`].input.menu.inbuild.menu;
  const peecee_input_external =
    dropdown_menu[`PEECEE_dropdown_menu`].input.menu.external.menu;
  const peecee_output_inbuild =
    dropdown_menu[`PEECEE_dropdown_menu`].output.menu.inbuild.menu;
  const peecee_output_external =
    dropdown_menu[`PEECEE_dropdown_menu`].output.menu.external.menu;
  const zing_input_inbuild =
    dropdown_menu[`ZING_dropdown_menu`].input.menu.inbuild.menu;
  const zing_input_external =
    dropdown_menu[`ZING_dropdown_menu`].input.menu.external.menu;
  const zing_output_inbuild =
    dropdown_menu[`ZING_dropdown_menu`].output.menu.inbuild.menu;
  const zing_output_external =
    dropdown_menu[`ZING_dropdown_menu`].output.menu.external.menu;

  const handleScroll = (event) => {
    if (event.currentTarget.scrollTop > 0) {
      scrollIndicator.current.style.display = "block";
    } else {
      scrollIndicator.current.style.display = "block";
    }

    // Check if scrolled to the bottom
    if (
      event.currentTarget.scrollHeight <=
      Math.ceil(event.currentTarget.scrollTop) +
        event.currentTarget.clientHeight
    ) {
      scrollIndicator.current.style.display = "none";
    }
  };
  //onScroll={(e) => handleScroll(e)}

  return (
    <>
      <div
        className="dropdownContent outside"
        id="droDown_outer"
        ref={sampleRef}
      >
        <div className="arrow"></div>
        <div
          className="droDown_inner"
          id="droDown_inner"
          onScroll={(e) => handleScroll(e)}
          style={{ overflowY: "scroll" }}
          ref={droDownInner}
        >
          <div
            className="dropdown"
            onClick={(event) => toggleDropDown(event, "mainRef1")}
          >
            <button className="dropbtn">PeeCee</button>
            <div className="dropdownContent" ref={mainRef1}>
              <div
                className="dropdown"
                onClick={(event) => toggleDropDown(event, "inputRef1")}
              >
                <button className="dropbtn margin">Input</button>
                <div ref={inputRef1} className="dropdownContent">
                  <div
                    className="dropdown"
                    onClick={(event) => toggleDropDown(event, "inbuiltRef1")}
                  >
                    <button
                      className="dropbtn"
                      style={{
                        marginLeft: "40px",
                        fontFamily: "Halcyon Regular",
                      }}
                    >
                      Inbuilt
                    </button>
                    <div ref={inbuiltRef1} className="dropdownContent">
                      {Object.keys(peecee_input_inbuild).map((element) => {
                        return (
                          <a
                            key={element}
                            href={"#" + element}
                            onClick={(e) => {
                              getSampleCode(e, element, "peecee");
                            }}
                          >
                            {peecee_input_inbuild[element].label}
                          </a>
                        );
                      })}
                      {/* <a
                      href="#Item1"
                      onClick={(e) => {
                        getSampleCode(e, "led_blink");
                      }}
                    >
                      Led Blinking
                    </a>
                    <a
                      href="#Item2"
                      onClick={(e) => {
                        getSampleCode(e, "GPIO_read");
                      }}
                    >
                      Reading from GPIO
                    </a>
                    <a
                      href="#Item3"
                      onClick={(e) => {
                        getSampleCode(e, "ADC_read");
                      }}
                    >
                      Reading ADC value
                    </a>
                    <a
                      href="#Item4"
                      onClick={(e) => {
                        getSampleCode(e, "smile_led");
                      }}
                    >
                      Smile Led
                    </a> */}
                    </div>
                  </div>
                  <div
                    className="dropdown"
                    onClick={(event) => toggleDropDown(event, "externalRef1")}
                  >
                    <button className="dropbtn" style={{ marginLeft: "40px" }}>
                      External
                    </button>
                    <div ref={externalRef1} className="dropdownContent">
                      {Object.keys(peecee_input_external).map((element) => {
                        return (
                          <a
                            key={element}
                            href={"#" + element}
                            onClick={(e) => {
                              getSampleCode(e, element, "peecee");
                            }}
                          >
                            {peecee_input_external[element].label}
                          </a>
                        );
                      })}
                      {/* <a href="#">Item1</a>
                    <a href="#">Item2</a>
                    <a href="#">Item3</a> */}
                    </div>
                  </div>
                </div>
              </div>
              <div
                className="dropdown"
                onClick={(event) => toggleDropDown(event, "outputRef1")}
              >
                <button className="dropbtn margin">Output</button>
                <div ref={outputRef1} className="dropdownContent">
                  <div
                    className="dropdown"
                    onClick={(event) =>
                      toggleDropDown(event, "outputInbuiltRef1")
                    }
                  >
                    <button
                      className="dropbtn j"
                      style={{ marginLeft: "40px" }}
                    >
                      Inbuilt
                    </button>

                    <div ref={outputInbuiltRef1} className="dropdownContent">
                      {Object.keys(peecee_output_inbuild).map((element) => {
                        return (
                          <a
                            key={element}
                            href={"#" + element}
                            onClick={(e) => {
                              getSampleCode(e, element, "peeCee");
                            }}
                          >
                            {peecee_output_inbuild[element].label}
                          </a>
                        );
                      })}
                    </div>
                  </div>
                  <div
                    className="dropdown"
                    onClick={(event) =>
                      toggleDropDown(event, "outputExternalRef1")
                    }
                  >
                    <button
                      className="dropbtn j"
                      style={{ marginLeft: "40px" }}
                    >
                      External
                    </button>
                    <div ref={outputExternalRef1} className="dropdownContent">
                      {Object.keys(peecee_output_external).map((element) => {
                        return (
                          <a
                            key={element}
                            href={"#" + element}
                            onClick={(e) => {
                              getSampleCode(e, element, "peeCee");
                            }}
                          >
                            {peecee_output_external[element].label}
                          </a>
                        );
                      })}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div
            className="dropdown"
            onClick={(event) => toggleDropDown(event, "mainRef2")}
          >
            <button className="dropbtn">Humanoid</button>
            <div className="dropdownContent" ref={mainRef2}>
              <div
                className="dropdown"
                onClick={(event) => toggleDropDown(event, "inputRef2")}
              >
                <button className="dropbtn margin">Input</button>
                <div ref={inputRef2} className="dropdownContent">
                  <div
                    className="dropdown"
                    onClick={(event) => toggleDropDown(event, "inbuiltRef2")}
                  >
                    <button className="dropbtn" style={{ marginLeft: "40px" }}>
                      Inbuilt
                    </button>
                    <div ref={inbuiltRef2} className="dropdownContent">
                      {Object.keys(zing_input_inbuild).map((element) => {
                        return (
                          <a
                            key={element}
                            href={"#zing_" + element}
                            onClick={(e) => {
                              getSampleCode(e, element, "zing");
                            }}
                          >
                            {zing_input_inbuild[element].label}
                          </a>
                        );
                      })}
                    </div>
                  </div>
                  <div
                    className="dropdown"
                    onClick={(event) => toggleDropDown(event, "externalRef2")}
                  >
                    <button className="dropbtn" style={{ marginLeft: "40px" }}>
                      External
                    </button>
                    <div ref={externalRef2} className="dropdownContent">
                      {Object.keys(zing_input_external).map((element) => {
                        return (
                          <a
                            key={element}
                            href={"#zing_" + element}
                            onClick={(e) => {
                              getSampleCode(e, element, "zing");
                            }}
                          >
                            {zing_input_external[element].label}
                          </a>
                        );
                      })}
                    </div>
                  </div>
                </div>
              </div>

              <div
                className="dropdown"
                onClick={(event) => toggleDropDown(event, "outputRef2")}
              >
                <button className="dropbtn margin">Output</button>
                <div ref={outputRef2} className="dropdownContent">
                  <div
                    className="dropdown"
                    onClick={(event) =>
                      toggleDropDown(event, "outputInbuiltRef2")
                    }
                  >
                    <button className="dropbtn" style={{ marginLeft: "40px" }}>
                      Inbuilt
                    </button>
                    <div ref={outputInbuiltRef2} className="dropdownContent">
                      {Object.keys(zing_output_inbuild).map((element) => {
                        return (
                          <a
                            key={element}
                            href={"#zing_" + element}
                            onClick={(e) => {
                              getSampleCode(e, element, "zing");
                            }}
                          >
                            {zing_output_inbuild[element].label}
                          </a>
                        );
                      })}
                    </div>
                  </div>

                  <div
                    className="dropdown"
                    onClick={(event) =>
                      toggleDropDown(event, "outputExternalRef2")
                    }
                  >
                    <button className="dropbtn" style={{ marginLeft: "40px" }}>
                      External
                    </button>
                    <div ref={outputExternalRef2} className="dropdownContent">
                      {Object.keys(zing_output_external).map((element) => {
                        return (
                          <a
                            key={element}
                            href={"#zing_" + element}
                            onClick={(e) => {
                              getSampleCode(e, element, "zing");
                            }}
                          >
                            {zing_output_external[element].label}
                          </a>
                        );
                      })}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          className="scroll-indicator"
          id="scrollIndicator"
          ref={scrollIndicator}
        >
          <img style={{ width: "30px" }} src={renderImage("downIcon")} />
        </div>
      </div>
    </>
  );
};

export default Dropdown;
