import { useContext, useRef } from 'react'
import renderImage from '../RenderImage'
import './SubHeader.css'
import ShareData from '../../ShareData'

function SubHeader() {
    const { isOverlayVisible, sidebar_filesName, subHeader_filesName, setSubHeader_filesName, workSpaceFile, setWorkSpaceFile } = useContext(ShareData)
    const subHeaderRef = useRef();
    const handelWorkspace = (eachFileName) => {
        setWorkSpaceFile(sidebar_filesName[eachFileName])
    }
    //close the open sub header file
    const close_subHeader_file = (e, eachFileName) => {
        let newSubHeader_filesName = [...subHeader_filesName]
        let length = newSubHeader_filesName.length
        newSubHeader_filesName.map((newSubHeader_eahFileName, ind) => {
            if (newSubHeader_eahFileName === eachFileName) {
                newSubHeader_filesName.splice(ind, 1)
                if (length === 1) {
                    setWorkSpaceFile({})
                } else if (ind + 1 < length) {
                    setWorkSpaceFile(sidebar_filesName[newSubHeader_filesName[ind]])
                } else {
                    setWorkSpaceFile(sidebar_filesName[newSubHeader_filesName[ind - 1]])
                }

                setSubHeader_filesName(newSubHeader_filesName)
            }
        })
    }

    function scrollHorizontally(event) {
        event.preventDefault();
        var delta = Math.max(-1, Math.min(1, (event.wheelDelta || -event.detail)));
        var scrollSpeed = 100; // Adjust the scrolling speed as needed

        event.target.scrollLeft -= delta * scrollSpeed;
    }


    return (<div className='sub-header-container' style={{ width: isOverlayVisible ? '50%' : '100%' }} ref={subHeaderRef} onWheel={(e) => scrollHorizontally(e)}>
        {subHeader_filesName.map((eachFileName, index) => <div key={index} onClick={(e) => handelWorkspace(eachFileName)} style={{ overflow: "hidden", backgroundColor: workSpaceFile.filename === eachFileName && '#8ACDEA', color: workSpaceFile.filename === eachFileName && 'white' }} className='sub-header-file-container' >
            <p>{eachFileName}</p>
            <img className='cross_button' onClick={(e) => {
                e.stopPropagation()
                close_subHeader_file(e, eachFileName)
            }}
                style={{ marginLeft: "10%" }} src={renderImage('crossButton')} />
        </div>)}
    </div>)
}
export default SubHeader