import "./ExampleCodePopup.css";
// import style from '../Save/Save.module.css';
import style from "../../Save/Save.module.css";
import renderImage from "../../RenderImage";
import {
  sample_code_zing,
  sample_code_peeCee,
  dropdown_menu,
  sample_code_crawlee,
  sample_code_klaw,
  sample_code_moki,
  //   ZING_dropdown_menu,
  //   PEECEE_dropdown_menu,
} from "../../Utils/data";
import { useContext, useState } from "react";
import CodeMirror from "@uiw/react-codemirror";
import { python } from "@codemirror/lang-python";
import ShareData from "../../../ShareData";

function ExampleCodePoppup({ setShowExamplePopUp }) {
  const {
    sidebar_filesName,
    setSidebar_FilesName,
    setSubHeader_filesName,
    subHeader_filesName,
    setWorkSpaceFile,
    isNewUi,
  } = useContext(ShareData);
  const urlParams = new URLSearchParams(window.location.search);
  const deviceName = ["peeCee", "zing", "crawlee", "klaw", "moki"].includes(
    urlParams.get("device")
  )
    ? urlParams.get("device")
    : "peeCee";
  const [copyed, setCopyed] = useState(false);
  const [sampleCode, setSampleCode] = useState(``);
  const [select_sampleCodeName, setSelect_sampleCodeNam] = useState("");
  const [isShowFileNameError, setIsShowFileNameError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [show_exportToolTip, setShow_exportToolTip] = useState(false);
  const zing_input_inbuild =
    dropdown_menu[`${deviceName.toUpperCase()}_dropdown_menu`].input.menu
      .inbuild.menu;
  const zing_input_external =
    dropdown_menu[`${deviceName.toUpperCase()}_dropdown_menu`].input.menu
      .external.menu;
  const zing_output_inbuild =
    dropdown_menu[`${deviceName.toUpperCase()}_dropdown_menu`].output.menu
      .inbuild.menu;
  const zing_output_external =
    dropdown_menu[`${deviceName.toUpperCase()}_dropdown_menu`].output.menu
      .external.menu;

  const handelCopy = async (e) => {
    if (sampleCode) {
      setCopyed(true);
      try {
        await navigator.clipboard.writeText(sampleCode);
      } catch (err) {
        console.log(err);
      }

      setTimeout(() => setCopyed(false), 600);
    }
  };

  const getSampleCode = (e, pyCodeId) => {
    let codePython = "";
    if (deviceName === "zing") {
      codePython = sample_code_zing[pyCodeId];
    } else if (deviceName === "peeCee") {
      codePython = sample_code_peeCee[pyCodeId];
    } else if (deviceName === "crawlee") {
      codePython = sample_code_crawlee[pyCodeId];
    } else if (deviceName === "klaw") {
      codePython = sample_code_klaw[pyCodeId];
    } else if (deviceName === "moki") {
      codePython = sample_code_moki[pyCodeId];
    }
    setSampleCode(codePython);
    setSelect_sampleCodeNam(pyCodeId);
  };

  const exportSampleCode = (e) => {
    let newFileName = select_sampleCodeName + ".py";

    if (!select_sampleCodeName) {
      setErrorMessage("You are not select any example code!");
      setIsShowFileNameError(true);
      setTimeout(() => setIsShowFileNameError(false), 600);
      return;
    }

    // if (Object.keys(sidebar_filesName).length >= 5) {
    //     setErrorMessage("You are create only 5 files!")
    //     setIsShowFileNameError(true)
    //     setTimeout(() => setIsShowFileNameError(false), 600)
    //     return
    // }

    //change the file name or sort the name if its required
    switch (select_sampleCodeName) {
      case "accelerometer_and_gyrometer":
        newFileName = "acc_gyr.py";
        break;
      case "RGB_Ultrasonic":
        newFileName = "rgbUltra.py";
        break;
      case "RGB_LEDS_inbuild":
        newFileName = "rgbInbuilt.py";
        break;
    }

    //if file name present in the sidebar than not create the file
    let isFileNamePresent = false;
    Object.keys(sidebar_filesName).map((eachFileName) => {
      if (eachFileName === newFileName) {
        isFileNamePresent = true;
      }
    });
    if (isFileNamePresent) {
      setErrorMessage("This file name already exists!");
      setIsShowFileNameError(true);
      setTimeout(() => setIsShowFileNameError(false), 600);
      return;
    }

    let key = newFileName;
    let new_Sidebar_FilesName = { ...sidebar_filesName };
    new_Sidebar_FilesName[key] = { filename: key, code: sampleCode };
    setSidebar_FilesName(new_Sidebar_FilesName);
    setWorkSpaceFile({ filename: key, code: sampleCode });
    setSubHeader_filesName([...subHeader_filesName, key]);

    setShowExamplePopUp(false);
  };

  return (
    <div className={style.container}>
      <div className="pupUpBox">
        <div style={{ display: "flex", justifyContent: "right" }}>
          {copyed ? (
            <p
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                marginRight: "20px",
                cursor: "pointer",
              }}
            >
              <img src={renderImage("clipBordCopyed")} alt="copy" />
              Copied
            </p>
          ) : (
            <p
              onClick={(e) => handelCopy()}
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                marginRight: "20px",
                cursor: sampleCode ? "pointer" : "default",
                opacity: sampleCode ? "1" : "0.5",
              }}
            >
              <img src={renderImage("clipBord")} alt="copy" />
              Copy
            </p>
          )}
          <img
            className="pupUpCloseButton"
            src={renderImage("closeButton")}
            alt="close"
            onClick={() => {
              setShowExamplePopUp(false);
            }}
          />
        </div>

        <div className="popPup-container">
          <div
            className="popPupCodeName-container"
            style={{ overflow: "auto" }}
          >
            {/*------------------- outputs code ---------------*/}
            <h4 style={{ marginBottom: "10px" }}>Input</h4>
            {Object.keys(zing_input_inbuild).map((element, index) => {
              let select = false;
              if (element === select_sampleCodeName) {
                select = true;
              }
              return (
                <p
                  key={element}
                  className={select && "select_sampleCode"}
                  onClick={(e) => {
                    getSampleCode(e, element);
                  }}
                  style={
                    index === 0
                      ? { borderTop: "1px rgb(101, 89, 89) solid" }
                      : {}
                  }
                >
                  {zing_input_inbuild[element].label}
                </p>
              );
            })}
            {Object.keys(zing_input_external).map((element) => {
              let select = false;
              if (element === select_sampleCodeName) {
                select = true;
              }
              return (
                <p
                  key={element}
                  className={select && "select_sampleCode"}
                  onClick={(e) => {
                    getSampleCode(e, element);
                  }}
                >
                  {zing_input_external[element].label}
                </p>
              );
            })}

            {/*------------------- outputs code ---------------*/}
            <h4 style={{ marginBottom: "10px", marginTop: "10px" }}>Output</h4>
            {Object.keys(zing_output_inbuild).map((element, index) => {
              let select = false;
              if (element === select_sampleCodeName) {
                select = true;
              }
              return (
                <p
                  key={element}
                  className={select && "select_sampleCode"}
                  onClick={(e) => {
                    getSampleCode(e, element);
                  }}
                  style={
                    index === 0
                      ? { borderTop: "1px rgb(101, 89, 89) solid" }
                      : {}
                  }
                >
                  {zing_output_inbuild[element].label}
                </p>
              );
            })}
            {Object.keys(zing_output_external).map((element, index) => {
              let select = false;
              if (element === select_sampleCodeName) {
                select = true;
              }
              return (
                <p
                  key={element}
                  className={select && "select_sampleCode"}
                  onClick={(e) => {
                    getSampleCode(e, element);
                  }}
                  style={
                    (zing_output_inbuild.length <= 0 ||
                      zing_output_inbuild.length == undefined) &&
                    index === 0
                      ? { borderTop: "1px rgb(101, 89, 89) solid" }
                      : {}
                  }
                >
                  {zing_output_external[element].label}
                </p>
              );
            })}
          </div>
          {/* <div className="popPupCode-container"></div> */}
          <CodeMirror
            value={sampleCode}
            height="60vh"
            lint={false}
            gutters={[]}
            readOnly={true}
            extensions={[python({ py: true })]}
            style={{
              width: "74%",
              fontSize: "18px",
            }}
          />
          <img
            className="exportButton"
            onMouseEnter={(e) => setShow_exportToolTip(true)}
            onMouseLeave={(e) => setShow_exportToolTip(false)}
            onClick={(e) => exportSampleCode()}
            src={renderImage("exportButton")}
          />
          {show_exportToolTip && <p className="exportToolTip">Export Code</p>}
          {isShowFileNameError && (
            <p className="errorFileName">{errorMessage}</p>
          )}
        </div>
      </div>
    </div>
  );
}

export default ExampleCodePoppup;
