      // const folder = zip.folder(`${fileName}`);
      // Object.keys(sidebar_filesName).map((eachObjKey) => {
      //     folder.file(
      //         `${eachObjKey}`,
      //         `${sidebar_filesName[eachObjKey].code}`
      //     );
      // });

      // // Generate the ZIP content
