import React, { useContext, useEffect, useRef, useState } from "react";
import renderImage from "../RenderImage";
import ShareData from "../../ShareData";
import JSZip from "jszip";
import SlideShow from "../SlideShow/SlideShow";
import sample from "../Utils/sample.json";

import {
  HeaderContainer,
  LeftContainer,
  RightContainer,
  SelectBarImg,
  DropdownDiv,
  DropButton,
  NavIconImg,
  File,
  Title,
} from "./styles";
import Dropdown from "../DropDown/Dropdown";
import SavePopUp from "../Save/SavePopUp";
import {
  dropdownList,
  sample_code_peeCee,
  sample_code_zing,
} from "../Utils/data";
import Lodar from "../Loder/Loder";
import ExampleCodePoppup from "./ExampleCode/ExampleCodePopup";
import InputFilePopup from "./InputFilePopup";

const filters = [
  { usbVendorId: 0x303a, usbProductId: 0x1001 },
  { usbVendorId: 0x1a86, usbProductId: 0x7523 },
  { usbVendorId: 0xb1b0, usbProductId: 0x8055 },
];
let openStatus = false;
let closeStatus = false;

function Header() {
  const {
    code,
    setCode,
    usb,
    setUsb,
    refreshByte,
    setRefreshByte,
    worker,
    device,
    setDevice,
    setFeedback,
    isDisabled,
    setIsDisabled,
    sidebar_filesName,
    setSidebar_FilesName,
    subHeader_filesName,
    setSubHeader_filesName,
    workSpaceFile,
    setWorkSpaceFile,
    isProjectRun,
    setIsProjectRun,
    isLodarShow,
    setIsLodarShow,
    projectName,
    setProjectName,
    isNewUi,
    setIsChange,
  } = useContext(ShareData);
  let zingEditar = {};
  try {
    zingEditar = JSON.parse(sessionStorage.getItem("zingEditar")) || {
      subHeader_filesName: [],
      sidebar_filesName: {},
      workSpaceFile: {},
      currentConsoleText: "",
      projectName: "Untitled",
    };
  } catch (e) {
    console.log("ERROR", e);
  }

  const [isPopup, setPopup] = useState(false);
  const [filePopup, setFilePopUp] = useState(false);
  const [showExamplePopUp, setShowExamplePopUp] = useState(false);
  // const [projectName, setProjectName] = useState(zingEditar?.projectName);
  const [editProjectName, setEditProjectName] = useState(false);
  const [isShowFileInputPopUp, setIsShowFileInputPopUp] = useState(false);
  const [isShowFileReplacePopUp, setIsShowFileReplacePopUp] = useState(false);
  const urlParams = new URLSearchParams(window.location.search);
  const [inputFileDetails, setInputFileDetails] = useState({
    fileName: "",
    code: "",
  });
  const deviceName = ["peeCee", "zing", "crawlee", "klaw", "moki"].includes(
    urlParams.get("device")
  )
    ? urlParams.get("device")
    : "peeCee";
  const scrollIndicator = useRef();
  const refs = {};
  const registerRef = (name) => (ref) => {
    refs[name] = ref;
  };
  // const inputFile = (event) => {
  //   let file = event.target.files[0];
  //   const reader = new FileReader();
  //   if (["zing", "peeCee"].includes(deviceName)) {
  //     reader.onload = (evt) => {
  //       // let new_sidebar_filesName = { ...sidebar_filesName }
  //       // Object.keys(new_sidebar_filesName).map((eachFileObjectKey) => {
  //       //     if (eachFileObjectKey === workSpaceFile.filename) {
  //       //         new_sidebar_filesName[eachFileObjectKey].code = evt.target.result
  //       //     }
  //       // })
  //       // setSidebar_FilesName(new_sidebar_filesName)
  //       // setWorkSpaceFile({ ...workSpaceFile, code: evt.target.result })

  //       // setCode(evt.target.result);
  //       let datas = JSON.parse(evt.target.result);
  //       setSidebar_FilesName({ ...datas });
  //       setWorkSpaceFile({});
  //       setSubHeader_filesName([]);
  //     };
  //   } else {
  //     reader.onload = (evt) => {
  //       setCode(evt.target.result);
  //     };
  //   }
  //   reader.readAsText(file);
  // };

  useEffect(() => {
    const windowOnFocus = () => {
      sessionStorage.setItem("windowFocus", "true");
    };
    const windowOnBlur = () => {
      sessionStorage.setItem("windowFocus", "false");
    };

    window.addEventListener("blur", windowOnBlur);
    window.addEventListener("focus", windowOnFocus);

    return () => {
      window.removeEventListener("blur", windowOnBlur);
      window.removeEventListener("focus", windowOnFocus);
    };
  }, []);

  //check the which device connect
  useEffect(() => {
    const runLoop = async () => {
      const urlParams = new URLSearchParams(window.location.search);
      // const deviceName = ["peeCee", "zing", "roboki"].includes(
      //   urlParams.get("device")
      // )
      //   ? urlParams.get("device")
      //   : "peeCee";
      // if (deviceName === "zing") {
      //   console.log("Zing");
      //   setDevice("zing");
      //   setIsDisabled(false);
      //   setFeedback("");
      // } else if (deviceName === "peeCee") {
      //   console.log("peecee");
      //   setDevice("peeCee");
      //   setIsDisabled(false);
      //   setFeedback("");
      // } else {
      //   console.log("roboki");
      //   setDevice("roboki");
      //   setIsDisabled(false);
      //   setFeedback("");
      // }
      console.log(deviceName);
      setDevice(deviceName);
      setIsDisabled(false);
      setFeedback("");
    };

    const getProject = async () => {
      await new Promise((resolve) => setTimeout(resolve, 2000));
      let fileArr = [];
      let newSidebar_filesName = {};
      //get project data from zing microPython board
      let removeFileCommend = `import os\rprint(os.listdir())\r`;
      let asciRemoveFileCommend = [...removeFileCommend].map((char) =>
        char.charCodeAt(0)
      );
      worker.postMessage({
        type: "getFiles",
        value: [3, ...asciRemoveFileCommend],
      });

      worker.onmessage = async (e) => {
        if (e.data.type === "getFileRead") {
          fileArr = eval(e.data.value);

          //remove the boot.py
          fileArr.map((ele, i) => {
            if (ele === "boot.py") {
              fileArr.splice(i, 1);
            }
          });
          // fileArr.map
          if (!fileArr.includes("main.py")) {
            newSidebar_filesName = {
              ...newSidebar_filesName,
              "main.py": { code: "", filename: "main.py" },
            };
          }
          // get each file Data
          fileArr.map(async (eachFileName) => {
            // let redFileCommend = `f = open("${eachFileName}", "r")\rprint(f.read())\rf.close()\r`
            // let asciRedFileCommend = [...redFileCommend].map((char) => char.charCodeAt(0))
            let removeFileCommend = `import os\ros.remove("${eachFileName}")\r`;
            let asciRemoveFileCommend = [...removeFileCommend].map((char) =>
              char.charCodeAt(0)
            );
            worker.postMessage({
              type: "ignoreConsolePrint",
              value: [3, ...asciRemoveFileCommend],
            });
          });
          await new Promise((resolve) => setTimeout(resolve, 1000));
          setIsLodarShow(false);
        }
      };
      await new Promise((resolve) => setTimeout(resolve, 7000));
      setIsLodarShow(false);
    };
    if (usb) {
      isNewUi && setIsLodarShow(true);
      runLoop();
      isNewUi && setIsLodarShow(false);
      // isNewUi && getProject();
    }
    if (!usb) {
      setFeedback("");
      setDevice(null);
    }
  }, [usb]);

  useEffect(() => {
    const openPort = async () => {
      try {
        if (!openStatus) {
          openStatus = true;
          worker.postMessage({
            type: "connected",
          });
          worker.onmessage = (event) => {
            if (event.data.type === "connected") {
              console.log("event event event", event);
              setUsb((prev) => event.data.value);
              openStatus = false;
            }
          };
        }
      } catch (e) {
        console.log(e);
      }
    };

    const handleConnect = async (e) => {
      if (JSON.parse(sessionStorage.getItem("windowFocus"))) {
        await openPort(e.target);
      }
    };

    const handleDisconnect = (e) => {
      closePort(e.target);
      // window.location.reload();
    };

    navigator.serial.getPorts(filters).then(async (ports) => {
      if (ports[0]) await openPort(ports[0]);
    });

    navigator.serial.addEventListener("connect", handleConnect);
    navigator.serial.addEventListener("disconnect", handleDisconnect);

    return () => {
      navigator.serial.removeEventListener("connect", handleConnect);
      navigator.serial.removeEventListener("disconnect", handleDisconnect);
    };
  }, [openStatus]);

  const closePopup = (text) => {
    setPopup(false);
  };

  const help = () => {
    setPopup(true);
  };
  const closePort = async (port) => {
    setRefreshByte(false);
    if (!closeStatus) {
      closeStatus = true;
      try {
        setUsb(false);
        worker.postMessage({
          type: "disconnected",
        });
        closeStatus = false;
      } catch (e) {
        console.log(e);
      }
    }
  };

  const requestPort = async () => {
    try {
      await navigator.serial
        .requestPort({
          filters,
        })
        .then(async () => {
          worker.postMessage({
            type: "connected",
          });
        });
      worker.onmessage = (event) => {
        if (event.data.type === "connected") {
          setUsb(event.data.value);
        }
      };
    } catch (e) {
      console.log(e);
    }
  };

  const showarrowButton = () => {
    let container = refs["droDownInner"];
    if (container.scrollHeight > container.clientHeight) {
      scrollIndicator.current.style.display = "block";
    }
    if (container.scrollHeight <= container.clientHeight) {
      scrollIndicator.current.style.display = "none";
    }
  };
  const closedropddown = (list, refName) => {
    if (refName === "sampleRef") {
      let drops = document.getElementsByClassName("dropdownContent");
      for (let i = 1; i < drops.length; i++) {
        drops[i].style.display = "none";
      }
    } else {
      let ele = refs[refName];
      ele.style.display = "none";
      list.map((eachref) => {
        let eachEle = refs[eachref];
        eachEle.style.display = "none";
      });
    }
  };
  const toggleDropDown = (e, refName) => {
    let ele = refs[refName];
    e.stopPropagation();
    const list = dropdownList[refName];
    switch (refName) {
      case "mainRef1":
        ele.style.display =
          ele.style.display === "block"
            ? closedropddown(list, "mainRef1")
            : "block";
        showarrowButton();
        return;
      case "inputRef1":
        ele.style.display =
          ele.style.display === "block"
            ? closedropddown(list, "inputRef1")
            : "block";
        showarrowButton();
        return;
      case "outputRef1":
        ele.style.display =
          ele.style.display === "block"
            ? closedropddown(list, "outputRef1")
            : "block";
        showarrowButton();
        return;
      case "mainRef2":
        ele.style.display =
          ele.style.display === "block"
            ? closedropddown(list, "mainRef2")
            : "block";
        showarrowButton();
        return;
      case "inputRef2":
        ele.style.display =
          ele.style.display === "block"
            ? closedropddown(list, "inputRef2")
            : "block";
        showarrowButton();
        return;
      case "outputRef2":
        ele.style.display =
          ele.style.display === "block"
            ? closedropddown(list, "outputRef2")
            : "block";
        showarrowButton();
        return;
      case "sampleRef":
        if (ele.classList.contains("flexClass")) {
          scrollIndicator.current.style.display = "none";
          ele.classList.remove("flexClass");
          closedropddown(list, "sampleRef");
        } else {
          ele.classList.add("flexClass");
        }
        return;
      case "droDownInner":
        ele.style.display = ele.style.display === "block" ? "none" : "block";
        return;
    }

    showarrowButton();
    ele.style.display = ele.style.display === "block" ? "none" : "block";
  };

  useEffect(() => {
    const handleClicks = (e) => {
      let dropDownOutside = document.getElementById("droDown_outer");
      let droDownInner = document.getElementById("droDown_inner");
      dropDownOutside.classList.remove("flexClass");
      droDownInner.style.display = "none";
      scrollIndicator.current.style.display = "none";

      let drops = document.getElementsByClassName("dropdownContent");
      for (let i = 1; i < drops.length; i++) {
        drops[i].style.display = "none";
      }
    };
    document.addEventListener("click", handleClicks);

    return () => {
      document.removeEventListener("click", handleClicks);
    };
  }, []);

  const getSampleCode = (e, pyCodeId, device) => {
    e.stopPropagation();
    if (device === "peeCee") {
      let codePython = sample_code_peeCee[pyCodeId];
      sessionStorage.setItem("code", JSON.stringify(codePython));
      setCode(codePython);
      sessionStorage.setItem("code", JSON.stringify(codePython));
    } else if (device === "zing") {
      let codePython = sample_code_zing[pyCodeId];
      sessionStorage.setItem("code", JSON.stringify(codePython));
      setCode(codePython);
      sessionStorage.setItem("code", JSON.stringify(codePython));
    }
  };

  const erase = (e) => {
    if (isNewUi) {
      setIsChange(true);
      let new_sidebar_filesName = { ...sidebar_filesName };
      Object.keys(new_sidebar_filesName).map((eachFileObjectKey) => {
        if (eachFileObjectKey === workSpaceFile.filename) {
          new_sidebar_filesName[eachFileObjectKey].code = "";
          setCode("");
        }
      });

      setSidebar_FilesName(new_sidebar_filesName);
      setWorkSpaceFile({ ...workSpaceFile, code: "" });
    } else {
      setCode("");
      sessionStorage.setItem("code", JSON.stringify(""));
    }
  };
  const Save = () => {
    if (isNewUi) {
      setIsChange(false);
      const zip = new JSZip();
      const folder = zip.folder(`${projectName}`);
      Object.keys(sidebar_filesName).map((eachObjKey) => {
        folder.file(`${eachObjKey}`, `${sidebar_filesName[eachObjKey].code}`);
      });

      // Generate the ZIP content
      zip.generateAsync({ type: "blob" }).then((blob) => {
        // Create a download link
        const link = document.createElement("a");
        link.href = URL.createObjectURL(blob);
        link.download = `${projectName}.zip`;
        document.body.appendChild(link);

        link.click();
        document.body.removeChild(link);
      });
    } else {
      setFilePopUp(true);
    }
  };

  const projectNameSubmitHandel = () => {
    setEditProjectName(false);
  };

  //toggle swaipe button
  const toggleSwipe = (e) => {
    var swipeContainer = document.querySelector(".swipe-container");
    swipeContainer.classList.toggle("true");
    setIsProjectRun(!isProjectRun);
  };

  const handelReplaceFile = (e) => {
    let newSidebar_filesName = {
      ...sidebar_filesName,
    };
    newSidebar_filesName[inputFileDetails.fileName] = {
      filename: inputFileDetails.fileName,
      code: inputFileDetails.code,
    };
    setSidebar_FilesName(newSidebar_filesName);
    setWorkSpaceFile({
      filename: inputFileDetails.fileName,
      code: inputFileDetails.code,
    });
    setIsShowFileReplacePopUp(false);
  };

  const getProject = async () => {
    let removeFileCommend = `import os\rprint(os.listdir())\r`;
    let asciRemoveFileCommend = [...removeFileCommend].map((char) =>
      char.charCodeAt(0)
    );
    worker.postMessage({
      type: "getFiles",
      value: [3, ...asciRemoveFileCommend],
    });
  };

  const getFilsData = async () => {
    let fileArr = ["a.py", "b.py", "boot.py", "c.py", "d.py", "main.py"];
    fileArr.map(async (eachFileName) => {
      await new Promise((resolve) => setTimeout(resolve, 2000));
      // await new Promise((resolve) => setTimeout(resolve, 2000))
      let redFileCommend = `f = open("${eachFileName}", "r")\rprint(f.read())\rf.close()\r`;
      let asciRedFileCommend = [...redFileCommend].map((char) =>
        char.charCodeAt(0)
      );
      worker.postMessage({
        type: "getFileData",
        value: [3, ...asciRedFileCommend],
      });
    });
  };

  return (
    <>
      <HeaderContainer>
        <LeftContainer>
          <SelectBarImg src={renderImage("selectbar")} />
          <Title>Python Coding</Title>

          {isNewUi && editProjectName ? (
            <form
              className="projectName_from"
              onSubmit={(e) => projectNameSubmitHandel()}
            >
              <input
                spellCheck="false"
                value={projectName}
                onChange={(e) => {
                  setIsChange(true);
                  setProjectName(e.target.value);
                }}
              ></input>
            </form>
          ) : (
            <div className="projectname">
              <p className="projectnameTitle">{projectName}</p>
              <div className="projectnameEditDiv">
                <img
                  className="editButton"
                  src={renderImage("editPencil")}
                  alt="edit"
                  onClick={(e) => setEditProjectName(true)}
                />
                <p className="editProjectToolTip">Edit Project Name</p>
              </div>
            </div>
          )}
        </LeftContainer>

        <RightContainer>
          {/* swaipe button for zing */}
          {isNewUi && (
            <div className="swipe-container" onClick={(e) => toggleSwipe(e)}>
              <div className="swipe-handle"></div>
              <div className="left-text">Project Run</div>
              <div className="right-text">File Run</div>
            </div>
          )}
          <NavIconImg
            onClick={(event) => {
              if (isNewUi) {
                setShowExamplePopUp(true);
              } else {
                toggleDropDown(event, "sampleRef");
                toggleDropDown(event, "droDownInner");
              }
            }}
            src={renderImage("code")}
            alt="example code"
            draggable="false"
            style={{ paddingTop: "0%", width: "7.5vh" }}
          />
          <NavIconImg
            src={renderImage("erase")}
            alt="erase"
            onClick={erase}
            draggable="false"
          />
          <File>
            <NavIconImg
              src={renderImage("file")}
              alt="file"
              draggable="false"
              onClick={(e) => setIsShowFileInputPopUp(true)}
            />
          </File>
          {/* <input
            id="file-input"
            type="file"
            accept=".py"
            hidden
            onChange={(e) => {
              inputFile(e);
            }}
          /> */}
          <NavIconImg
            src={renderImage("save")}
            alt="help"
            onClick={() => {
              Save();
            }}
            draggable="false"
          />
          <img
            style={{
              width: "15vh",
              cursor: "pointer",
              marginTop: "1%",
            }}
            id="wifiOff"
            src={renderImage(usb ? "usbOn" : "usbOff")}
            alt="wifi"
            onClick={requestPort}
            draggable="false"
          />
        </RightContainer>
      </HeaderContainer>
      {filePopup && (
        <SavePopUp
          showPopUp={filePopup}
          setFilePopUp={setFilePopUp}
          code={code}
        />
      )}

      {/* ---------------take the file or folder input popup------------ */}
      {isShowFileInputPopUp && (
        <InputFilePopup
          setIsShowFileInputPopUp={setIsShowFileInputPopUp}
          setIsShowFileReplacePopUp={setIsShowFileReplacePopUp}
          setInputFileDetails={setInputFileDetails}
        />
      )}

      {/* ---------------show the replace file popup------------ */}
      {isShowFileReplacePopUp && (
        <div className="inputFilePopupContainer">
          <div
            className="inputFilePopupContainer_inner"
            style={{ flexDirection: "column" }}
          >
            <p style={{ marginTop: "25px", textAlign: "center" }}>
              An older file named{" "}
              <span style={{ color: "red" }}>{inputFileDetails.fileName}</span>{" "}
              already exists. Do you want to replace it with the newer one?
            </p>

            <div className="sameFile_exists_ans">
              <p onClick={handelReplaceFile}>YES</p>
              <p onClick={(e) => setIsShowFileReplacePopUp(false)}>NO</p>
            </div>
          </div>
        </div>
      )}

      <Dropdown
        toggleDropDown={toggleDropDown}
        registerRef={registerRef}
        getSampleCode={getSampleCode}
        scrollIndicator={scrollIndicator}
      />
      {isNewUi && showExamplePopUp && (
        <ExampleCodePoppup
          showExamplePopUp={showExamplePopUp}
          setShowExamplePopUp={setShowExamplePopUp}
        />
      )}
      {isNewUi && isLodarShow && (
        <div
          style={{
            zIndex: "1000000",
            width: "100vw",
            height: "100vh",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Lodar />
        </div>
      )}
    </>
  );
}
export default Header;
