import "./App.css";
import React, { useEffect, useRef, useState } from "react";
import Header from "./Components/Header/Header";
import Footer from "./Components/Footer/Footer";
import CodeMirror from "@uiw/react-codemirror";
import { python } from "@codemirror/lang-python";
import { PythonProvider } from "react-py";
import { styled } from "styled-components";
import ShareData from "./ShareData";
import Console from "./Components/Console/Console";
import SideBar from "./Components/leftSideBar/SideBar";
import SubHeader from "./Components/Header/SubHeader";
import ErasingPopup from "./Components/Footer/ErasingPopup";
const packages = {
  micropip: ["numpy", "pandas", "asciitree"],
};

const urlParams = new URLSearchParams(window.location.search);
const deviceName = ["peeCee", "zing", "crawlee", "klaw", "moki"].includes(
  urlParams.get("device")
)
  ? urlParams.get("device")
  : "peeCee";
const isNewUi = ["peeCee", "zing", "crawlee", "klaw", "moki"].includes(
  deviceName
);

const MainContainer = styled.div`
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 11vh 76vh 13vh;
  /* z-index: 1; */
`;

const BodyContainer = styled.div`
  /* Adjust this value to fit your needs */
  padding-top: 5px;
  z-index: 1;
  display: flex;
  position: fixed;
  top: ${isNewUi ? "17.5vh" : "10.5vh"};
  left: ${isNewUi ? "15%" : "0"};
  width: 100%;
  height: 70vh;
`;
const FooterContainer = styled.div`
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: white;
  z-index: 1;
  overflow: hidden;
  padding-bottom: 1%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  // transition: width 0.2s ease-in-out;
  // transition: width 0.4s ease-in-out;
`;
const BodySubContainer = styled.div`
  width: 5%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  // transition: border-right 0.4s ease-in-out;
  // transition: width 0.4s ease-in-out;
`;
const CODE = JSON.parse(sessionStorage.getItem("code")) || "";
var newFileName = "";

function PythonEditor(props) {
  let zingEditar = {};
  try {
    zingEditar = JSON.parse(sessionStorage.getItem("zingEditar")) || {
      subHeader_filesName: [],
      sidebar_filesName: {
        "main.py": { code: "", filename: "main.py" },
      },
      workSpaceFile: {},
      currentConsoleText: "",
      projectName: "Untitled",
    };
  } catch (e) {
    console.log("ERROR", e);
  }

  // console.log("BBBBBBBBBBB", JSON.parse(sessionStorage.getItem("zingEditar")));
  const [code, setCode] = useState(CODE);
  const [usb, setUsb] = useState(false);
  const [device, setDevice] = useState(null);
  const [refreshByte, setRefreshByte] = useState(
    JSON.parse(sessionStorage.getItem("refreshByte")) || false
  );

  const [worker, setWorker] = useState(props.worker);
  const [isOverlayVisible, setIsOverlayVisible] = useState(false);
  const [messages, setMessages] = useState("");
  // const [messages, setMessages] = useState("")
  const [isDisabled, setIsDisabled] = useState(false);

  const [feedback, setFeedback] = useState("");
  const [isProjectRun, setIsProjectRun] = useState(true);
  const consoleRef = useRef();
  const [isLodarShow, setIsLodarShow] = useState(false);
  const [isChange, setIsChange] = useState(false);
  const [projectName, setProjectName] = useState(zingEditar.projectName);
  const [sidebar_filesName, setSidebar_FilesName] = useState(
    // (JSON.parse(sessionStorage.getItem("zingEditar")) &&
    //   JSON.parse(sessionStorage.getItem("zingEditar")).sidebar_filesName) || {
    //   "main.py": { code: "", filename: "main.py" },
    // }
    zingEditar.sidebar_filesName
  );
  const [subHeader_filesName, setSubHeader_filesName] = useState(
    // (JSON.parse(sessionStorage.getItem("zingEditar")) &&
    //   JSON.parse(sessionStorage.getItem("zingEditar")).subHeader_filesName) ||
    //   []
    zingEditar.subHeader_filesName
  );
  const [workSpaceFile, setWorkSpaceFile] = useState(
    // (JSON.parse(sessionStorage.getItem("zingEditar")) &&
    //   JSON.parse(sessionStorage.getItem("zingEditar")).workSpaceFile) ||
    //   {}
    zingEditar.workSpaceFile
  );

  useEffect(() => {
    zingEditar = {
      ...zingEditar,
      sidebar_filesName: sidebar_filesName,
      subHeader_filesName: subHeader_filesName,
      workSpaceFile: workSpaceFile,
      projectName: projectName,
    };
    sessionStorage.setItem("zingEditar", JSON.stringify(zingEditar));
  }, [sidebar_filesName, subHeader_filesName, workSpaceFile, projectName]);

  useEffect(() => {
    return sessionStorage.setItem("refreshByte", JSON.stringify(refreshByte));
  }, [refreshByte]);

  //console
  const handleOverLay = () => {
    setIsOverlayVisible(!isOverlayVisible);
  };

  //clear button on console
  const clearConsole = () => {
    setMessages("");
  };

  //add code to storage
  useEffect(() => {
    sessionStorage.setItem("code", JSON.stringify(code));
  }, [code]);

  //not erased the console text after it close
  useEffect(() => {
    if (!isOverlayVisible) {
      if (messages !== "") {
        zingEditar = {
          ...zingEditar,
          currentConsoleText: messages,
        };
        sessionStorage.setItem("zingEditar", JSON.stringify(zingEditar));
      } else {
        setMessages(zingEditar.currentConsoleText);
      }
    } else {
      setMessages(zingEditar.currentConsoleText);
    }
  }, [isOverlayVisible]);

  //for refresh the zing page is sve the console Text
  window.onbeforeunload = function (event) {
    if (isNewUi) {
      zingEditar = {
        ...zingEditar,
        currentConsoleText: messages,
      };
      sessionStorage.setItem("zingEditar", JSON.stringify(zingEditar));
    }
  };

  return (
    <ShareData.Provider
      value={{
        code,
        setCode,
        usb,
        setUsb,
        refreshByte,
        setRefreshByte,
        worker,
        isOverlayVisible,
        setIsOverlayVisible,
        handleOverLay,
        messages,
        setMessages,
        clearConsole,
        device,
        setDevice,
        consoleRef,
        feedback,
        setFeedback,
        isDisabled,
        setIsDisabled,
        sidebar_filesName,
        setSidebar_FilesName,
        subHeader_filesName,
        setSubHeader_filesName,
        workSpaceFile,
        setWorkSpaceFile,
        isProjectRun,
        setIsProjectRun,
        isLodarShow,
        setIsLodarShow,
        projectName,
        setProjectName,
        isNewUi,
        isChange,
        setIsChange,
      }}
    >
      {/* <PythonProvider packages={packages}> */}
      <MainContainer>
        <Header />
        {isNewUi && <SubHeader />}
        {isNewUi && <SideBar />}
        <BodyContainer>
          {/* for zing code space if any file active in subheder */}
          {isNewUi ? (
            workSpaceFile.filename ? (
              <CodeMirror
                value={isNewUi ? workSpaceFile.code : code}
                height="67vh"
                lint={false}
                gutters={[]}
                extensions={[python({ py: true })]}
                onChange={(e) => {
                  // if (deviceName === "zing") {
                  let new_sidebar_filesName = { ...sidebar_filesName };
                  Object.keys(new_sidebar_filesName).map(
                    (eachFileObjectKey) => {
                      if (eachFileObjectKey === workSpaceFile.filename) {
                        new_sidebar_filesName[eachFileObjectKey].code = e;
                      }
                    }
                  );
                  setIsChange(true);
                  setSidebar_FilesName(new_sidebar_filesName);
                  setWorkSpaceFile({ ...workSpaceFile, code: e });
                  // }
                }}
                style={{
                  // overflowY: "auto",
                  width: `${isOverlayVisible ? "65%" : "80%"}`,
                  transition: "width 0.2s ease-in-out",
                  fontSize: "18px",
                }}
              />
            ) : (
              <div
                style={{
                  height: "69vh",
                  width: `${isOverlayVisible ? "65%" : "80%"}`,
                }}
              ></div>
            ) //in zing if any active file not present in subheader show empty div
          ) : (
            <CodeMirror //for peecee v1.0 codespace
              value={code}
              height="69vh"
              lint={false}
              gutters={[]}
              extensions={[python({ py: true })]}
              onChange={(e) => {
                setCode(e);
              }}
              style={{
                // overflowY: "auto",
                width: `${isOverlayVisible ? "65%" : "95%"}`,
                transition: "width 0.2s ease-in-out",
                fontSize: "18px",
              }}
            />
          )}
          <BodySubContainer>
            <Console />
          </BodySubContainer>
        </BodyContainer>
        <FooterContainer
          style={{ width: `${isOverlayVisible ? "65%" : "100%"}` }}
        >
          <Footer />
        </FooterContainer>
      </MainContainer>
      {/* </PythonProvider> */}
    </ShareData.Provider>
  );
}

export default PythonEditor;
