//sample code object for peecee
const sample_code_peeCee = {
  //inputs
  GPIO_read: `from playcomputer import *
from machine import Pin
from time import sleep
A1=Pin(PORT_A1,Pin.IN)

while True:
    A1_val=A1.value()
    sleep(1)
    print(A1_val)`,
  ADC_rea: `from playcomputer import PORT_A1, Pin , ADC
from time import sleep
pot = ADC(Pin(PORT_A1))
pot.atten(ADC.ATTN_11DB)       #Full range: 3.3v

while True:
    pot_value = pot.read()
    print(pot_value)
    sleep(0.1)`,
  temperature: `from playcomputer import *
from time import sleep

temp = ADC(Pin(TEMP_PIN))
temp.atten(ADC.ATTN_11DB)

while True:
    t = temp.read()
    print(t)
    sleep(1)`,
  mic: `from playcomputer import Mic
mic = Mic()
while True:
   val = mic.read()
   print(f"Sound : {val}")`,
  touch: `from playcomputer import TouchPad, Pin ,  PORT_T0, PORT_T1, PORT_T2, PORT_T3

t0 = TouchPad(Pin(PORT_T0))
t1 = TouchPad(Pin(PORT_T1))
t2 = TouchPad(Pin(PORT_T2))
t3 = TouchPad(Pin(PORT_T3))

while True:
   t0_val = t0.read()
   t1_val = t1.read()
   t2_val = t2.read()
   t3_val = t3.read()
   print(f"Touch 0 : {t0_val}")
   print(f"Touch 1 : {t1_val}")
   print(f"Touch 2 : {t2_val}")
   print(f"Touch 3 : {t3_val}")`,
  color_sensor: `from playcomputer import Apds
from time import sleep

apds = Apds()
apds.enable_color_sensor()

while True:
  red = apds.read_color_red()
  green = apds.read_color_green()
  blue = apds.read_color_blue()
  print(f"RED {red} GREEN {green} BLUE {blue}")`,
  light_sensor: `from playcomputer import Apds
from time import sleep

apds = Apds()
apds.enable_light_sensor()
   
while True:
    l = apds.read_light()
    print(l)
    sleep(1)`,
  gesture_sensor: `from playcomputer import Apds
from time import sleep

apds = Apds()
apds.enable_gesture_sensor()
   
while True:
    g = apds.read_gesture()
    print(g)
    sleep(1)`,
  proximity_sensor: `from playcomputer import *
from time import sleep

apds = Apds()
apds.enable_proximity_sensor()
   
while True:
    p = apds.read_proximity()
    print(p)
    sleep(1)`,
  battery: `from playcomputer import *
from time import sleep
   
bat = Battery()

while True:
    b = bat.get_level()
    print(b)
    sleep(1)`,
  accelerometer_and_gyrometer: `from playcomputer import Qmi8658
qmi8658=Qmi8658()

while True:
    xyz=qmi8658.read_xyz()
    print(f"ACC_X={xyz[0]}")
    print(f"ACC_Y={xyz[1]}")
    print(f"ACC_Z={xyz[2]}")

    print(f"GYRO_X={xyz[3]}")
    print(f"GYRO_Y={xyz[4]}")
    print(f"GYRO_Z={xyz[5]}")
`,

  // outputs
  led_blink: `from playcomputer import PORT_A1, Pin
from time import sleep

port = Pin(PORT_A1, Pin.OUT)

while True:
   port.value(1)
   sleep(1)
   port.value(0)
   sleep(1)
    `,
  pwm: `import playcomputer
from machine import PWM
            
pwm = PWM(playcomputer.PORT_A1 )  # PORT_A1: Here, 'A1' indicates the  port, based on what you used.
                                                   
pwm.duty_u16(32768)     # set duty (32768)  to 50% meens it glow 50% brightness.`,
  RGB_LEDS_eye: `from playcomputer import RgbEyes

eyes = RgbEyes()
eyes.set_pixels(num =1, red =255, blue =255, green =255)
eyes.update_pixels()`,
  smile_LED: `from playcomputer import SmileLed
from time import sleep
smile = SmileLed()

while True:
    smile.on(1)
    smile.on(2)
    smile.on(3)
    smile.on(4)
    sleep(1)

    smile.off(1)
    smile.off(2)
    smile.off(3)
    smile.off(4)
    sleep(1)`,
  buzzer: `from playcomputer import Buzzer
buzzer = Buzzer()

buzzer.start()
buzzer.set_freq(1000)
buzzer.stop()`,
  oled: `from playcomputer import Oled
oled=Oled()

# connect the oled in B port.

# set_text('string', x_coordinate, y_coordinate) is a method that takes 3 arguments:
# 1st argument: The string to be displayed, maximum string length is 16.
# 2nd argument: The X-coordinate on the OLED display where the string will be shown.
# 3rd argument: The Y-coordinate on the OLED display where the string will be shown.

# The valid range for the X-coordinate is from 0 to 128.
# The valid range for the Y-coordinate is from 0 to 64.

while True:
    oled.set_text("Free the MALLOCS !!!!", 10, 20)
    oled.show()`,

  rgb: `import playcomputer
import machine, neopixel
np = neopixel.NeoPixel(machine.Pin(playcomputer.PORT_B1), 10)

# rgb are connect at "B" port.
# np[0]: Here, '[0]' indicates the  rgb number 0.

np[0] = (255, 0, 0) # set to red, full brightness
np[1] = (0, 128, 0) # set to green, half brightness
np.write()`,
};

