function ErasingPopup({ setNo, setYes }) {
  return createPortal(
    <div className="erasePopupMain" style={{}}>
      <div className="erasePopupBox">
        <div className="erasePopupText">
          All Unsaved program will be Erased. Are you sure want to continue ?
        </div>
        <div className="btnGroup">
          <button className="erasePopupBtn" onClick={() => setYes()}>
            YES
          </button>
          <button className="erasePopupBtn" onClick={() => setNo()}>
            NO
          </button>
        </div>
      </div>
    </div>,
    document.body
  );
}
export default ErasingPopup;
