import { useContext } from "react";
import ShareData from "../../ShareData";
import renderImage from "../RenderImage";

function InputFilePopup({
  setIsShowFileInputPopUp,
  setIsShowFileReplacePopUp,
  setInputFileDetails,
}) {
  const {
    code,
    setCode,
    usb,
    setUsb,
    refreshByte,
    setRefreshByte,
    worker,
    device,
    setDevice,
    setFeedback,
    isDisabled,
    setIsDisabled,
    sidebar_filesName,
    setSidebar_FilesName,
    subHeader_filesName,
    setSubHeader_filesName,
    workSpaceFile,
    setWorkSpaceFile,
    isProjectRun,
    setIsProjectRun,
    isLodarShow,
    setIsLodarShow,
    setProjectName,
    isNewUi,
  } = useContext(ShareData);
  const urlParams = new URLSearchParams(window.location.search);
  const deviceName = ["peeCee", "zing", "crawlee", "klaw", "moki"].includes(
    urlParams.get("device")
  )
    ? urlParams.get("device")
    : "peeCee";

  //handel file upload
  const inputFile = (event) => {
    let file = event.target.files[0];

    const reader = new FileReader();
    if (isNewUi) {
      reader.onload = (evt) => {
        let code = evt.target.result;
        let fName = file.name;

        //if file name present in the sidebar than not create the file
        let isFileNamePresent = false;
        isFileNamePresent = Object.keys(sidebar_filesName).some(
          (eachFileName) => eachFileName === fName
        );
        if (isFileNamePresent) {
          setIsShowFileReplacePopUp(true);
          setInputFileDetails({
            fileName: fName,
            code: code,
          });
        } else {
          let newSidebar_filesName = {
            ...sidebar_filesName,
          };
          newSidebar_filesName[fName] = { filename: fName, code: code };
          setSidebar_FilesName(newSidebar_filesName);
          setWorkSpaceFile({ filename: fName, code: code });
          setSubHeader_filesName([...subHeader_filesName, fName]);
        }
      };
    } else {
      reader.onload = (evt) => {
        setCode(evt.target.result);
      };
    }
    setIsShowFileInputPopUp(false);
    reader.readAsText(file);
  };

  //handel folder upload
  const handleFolderUpload = (event) => {
    const files = event.target.files;
    let newSidebar_FilesName = {};
    const firstFilePath = files[0].webkitRelativePath;
    const folderName = firstFilePath.split("/")[0];
    setProjectName(folderName);

    Array.from(files).forEach(async (file) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        newSidebar_FilesName[file.name] = {
          filename: file.name,
          code: e.target.result,
        };
        setSidebar_FilesName(newSidebar_FilesName);
      };

      reader.readAsText(file);
    });
    // setProjectName(f)
    setIsShowFileInputPopUp(false);
    setWorkSpaceFile({});
    setSubHeader_filesName([]);
    event.target.value = "";
  };

  return (
    <div className="inputFilePopupContainer">
      <div className="inputFilePopupContainer_inner">
        <img
          className={"inputFilePopupContainer_closeBtn"}
          src={renderImage("close")}
          alt="close"
          onClick={() => {
            setIsShowFileInputPopUp(false);
          }}
        />
        <label htmlFor="file-input" className="file_div">
          <img src={renderImage("fileIcon")} alt="inputFile" />
          <p>Open File</p>
          <input
            id="file-input"
            type="file"
            accept=".py"
            hidden
            onChange={(e) => {
              inputFile(e);
            }}
          />
        </label>
        <label htmlFor="folder-input" className="file_div">
          <img src={renderImage("fileIcon")} alt="inputProject" />
          <p>Open Folder</p>
          <input
            id="folder-input"
            type="file"
            webkitdirectory="true"
            mozdirectory="true"
            hidden
            onChange={handleFolderUpload}
          />
        </label>
      </div>
    </div>
  );
}

export default InputFilePopup;
